h1{
    display: flex;
    justify-content: center;
    align-items: center;
}

.App{
    display:flex;
    justify-content: center;
    align-items:center;
    width: 100%;
    margin-top: 5em;
}

.calc-wrap{
    width:600px;
    height:800px;
}

.row{
    display: flex;
    width: 100%;
}

.result{
    display: flex;
    height: 4em;
    flex:1;
    background-color: black;
    color: white;
    justify-content: flex-end;
    align-items: center;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    outline: 1px solid black;
    font-weight: bold ;
    overflow: scroll;
}